*{
 margin:0px;
 padding: 0PX;
}
.wrapper{
  background-color: whitesmoke;
  height:auto;

}
.main1{
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: whitesmoke;
padding:5%;
}
.main3{
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: whitesmoke;
padding:5%;

}
.payment{
  background-color: whitesmoke;
  height:auto;

}

.givecharityhere{
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: whitesmoke;

}
.clickforcontact{
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: whitesmoke;

}
.charityhere{
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: whitesmoke;

}
.main4{
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: whitesmoke;
padding:5%;

}
.cheerful{
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: whitesmoke;
  margin-top: 10%;

}
.paragraph{
 font-size: 35px;
  color:black; 
   font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-weight:bold;
    margin-top: 5%;
}

.form {
width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
background-color: whitesmoke;
}


input {
  display: block;
  width: 33%;
  margin-bottom: 30px;
  padding: 15px;
  border-radius: 10px;
  border-top: none;
  border-left: none;
  border-right: none;
  background-color: white;
  

 
}
input::placeholder{
  font-size: 17px;
}


textarea{
  display: block;
  width: 50%;
  margin-bottom: 30px;
  padding: 10px;
  border-radius: 10px;
  border-top: none;
  border-left: none;
  border-right: none;
border-bottom: 2px solid rgb(102, 101, 101);
height: 15px;
background-color: white;
}
input::after{
  border:none;
}



.btn1 {
  align-items: center;
  height: 50px;
  margin-left: 5px;
  background: rgb(88, 151, 211);
  outline: none;
  border: none;
  color: #fff;
  font-size: 1.6rem;
  display: block;
  margin-bottom: 30px;
 width:430px;
  border-radius: 10px;
  border-top: none;
  border-left: none;
  border-right: none;
 
}
.btn1:hover{
   cursor: pointer;
  background-color: rgb(90, 199, 226);
  color: rgb(20, 20, 20);

}

.btn2{
  align-items: center;
  width: 430px;
  height: 50px;
  margin-left: 5px;
  background: rgb(146, 35, 190);
  outline: none;
  border: none;
  color: #fff;
  font-size: 1.4rem;
  display: block;
  margin-bottom: 30px;
  border-radius: 10px;
  border-top: none;
  border-left: none;
  border-right: none;

}
.btn2:hover{
  cursor: pointer;
}


.fulldiv{
  background-color: whitesmoke;
}


.one{
  display: flex;
  flex-direction: row;
  background-color: whitesmoke;
  width:auto;

}
.add-donor{
  margin-top: 5%;

}
.logout{

  
  color:white;
}

.donationentry{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: whitesmoke;

}

   
.footer{
  background: white;
  height:50px;
  display: flex;
  flex-direction: row;
  align-items: center;
}








