.FormGroup{
    
    padding: 0px;
    border-style: none;
    border-radius: 4px;
    
}
.finalpaymentform{
    width: 400px;
    height: 200px;
    margin-left: 22%;

}
.formRow{
   
    display:flex;
    align-items: center;
    flex-direction: column;
    width: 400px;
    height: 200px;
    background: whitesmoke;
}

.StripeElement{
    width:400px;
    height:40px;
    background-color: white;
    font-size: 32px;
}
.cardholder{
    width:370px;
    border: none;
}
.btn4{
    width: 150px;
    height: 30px;
}
@media screen and (min-width: 250px) and (max-width: 1280px) {

    .FormGroup{
    
        padding: 0px;
        border-style: none;
        border-radius: 4px;
        
    }
    .finalpaymentform{
        width: 400px;
        height: 200px;
       
    
    }
    .formRow{
       
        display:flex;
        align-items: center;
        flex-direction: column;
        width: 400px;
        height: 200px;
        background: whitesmoke;
    }
    
    .StripeElement{
        width:400px;
        height:40px;
        background-color: white;
        font-size: 32px;
    }
    .cardholder{
        width:370px;
        border: none;
    }
    .btn4{
        width: 150px;
        height: 30px;
    }

}
