*{
    margin:0px;
    padding: 0PX;
   }
   .main1{
     height: auto;
     display: flex;
     flex-direction: column;
     justify-content: center;
     align-items: center;
     background-color: whitesmoke;

   
   }
  
  .form-group{
    width:600px;
    height:auto;
    background-color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  label{
      width:80%;
  }
  .form-control{
    margin-top: 10%;
      width: 80%;
  }
  .password-icon{
      position: absolute;
      top:132%;
      right:35%;
      cursor:pointer;
  }

  .btn5{
    align-items: center;
  height: 50px;
  margin-left: 5px;
  background: rgb(88, 151, 211);
  outline: none;
  border: none;
  color: #fff;
  font-size: 1.6rem;
  display: block;
  margin-bottom: 30px;
 width:auto;
  border-radius: 10px;
  border-top: none;
  border-left: none;
  border-right: none;
  }
  .footer{
    background: white;
    height:70px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
  }

